#home {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Righteous", cursive;
}

.home-cards-container {
  margin: 25px;
  width: min(600px, 80%);
  text-align: center;
  background-color: darkcyan;
  border: 5px darkcyan solid;
  box-shadow: 0px 0px 10px darkcyan;
  border-radius: 10px;
}

.home-cards {
  text-align: center;
  width: 100%;
  min-width: fit-content;
  height: 200px;
  border: 1px black solid;
  border-radius: 10px;
  background-color: darkcyan;
}

.home-card-text {
  color: rgb(240, 240, 240);
  text-shadow: 3px 3px black;
  font-size: 2rem;
  margin: 5px;
}

#char-home-card {
  background-image: url(../images/r&m-characters.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
#location-home-card {
  background-image: url(../images/r&m-locations.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
#episode-home-card {
  background-image: url(../images/r&m-episodes.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.error-message {
  text-align: center;
  font-size: 20px;
}
